.songs
    .section-title
        margin:  0 0 40px
    &-body
        display: grid
        grid-template-columns: repeat(4, 1fr)
        gap: 20px 91px
    &-card
        border-radius: 16px 16px 0 0

        &__descr
            padding: 12px 32px
            background: linear-gradient(180deg, rgba(0, 137, 242, 0.4) 0%, rgba(250, 250, 250, 0.08) 100%)
            border-radius: 0px 0px 32px 32px
        &__title
            margin: 0 0 4px
            font-weight: 700
            font-size: 16px
            line-height: 140%
        &__subtitle
            font-weight: 400
            font-size: 14px
            line-height: 140%